wrap: pretty;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/**\n * OPTIONAL: Create a root stacking context\n *\n * This is beneficial since it allows us to guarantee that certain\n * high-priority elements (modals, dropdowns, tooltips) will always show up\n * above the other elements in our application. No weird stacking context bugs\n * no z-index arms race.\n *\n * !You should tweak the selector to match your framework.\n * We want to select the top-level element that your application is rendered\n * within. For example, create-react-app uses a <div id=\"root\">, so the correct\n * selector is #root.\n */\n/**\n * #root,\n * #__next {\n *  isolation: isolate;\n * }\n */\n\n/**\n * This is to stop animations for people that explicitly notified their system\n * that they prefer reduced motion. \n */\n@media screen and (prefers-reduced-motion: reduce) {\n  * {\n    /* With very short durations, relying on events JavaScript still works */\n    transition-duration: 0.001ms !important;\n    animation-duration: 0.001ms !important;\n    animation-iteration-count: 1 !important;\n  }\n}\n\n/**\n * Ideally, use `prefers-contrast`, `inverted-colors`, `prefers-color-scheme`\n * media features as well in your projects\n */\n","/*------------------------------------*\\\n  #FONTS\n\\*------------------------------------*/\n\n/* This file contains all @font-face declarations. */\n\n@font-face {\n  font-family: 'Bona Nova';\n  src:\n    local('Bona Nova'),\n    url('@assets/fonts/bonanova-regular.woff2') format('woff2'),\n    url('@assets/fonts/bonanova-regular.woff') format('woff');\n  font-weight: 400;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Bona Nova';\n  src:\n    local('Bona Nova'),\n    url('@assets/fonts/bonanova-bold.woff2') format('woff2'),\n    url('@assets/fonts/bonanova-bold.woff') format('woff');\n  font-weight: 700;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Noto Sans';\n  src:\n    local('Noto Sans'),\n    url('@assets/fonts/notosans-regular.woff2') format('woff2'),\n    url('@assets/fonts/notosans-regular.woff') format('woff');\n  font-weight: 400;\n  font-style: normal;\n}\n","%flex-col {\n  display: flex;\n  flex-direction: column;\n}\n\n%secondary-title {\n  font-family: var(--title-font);\n  font-size: var(--h2-size);\n  font-weight: 400;\n}\n\n%tertiary-title {\n  font-family: var(--title-font);\n  font-size: var(--h3-size);\n  font-weight: 400;\n}\n","/*------------------------------------*\\\n  #SOCIAL-LINKS\n\\*------------------------------------*/\n\n.social-links {\n  display: flex;\n  gap: 1.75rem;\n  padding: 0;\n  list-style-type: none;\n\n  &__icon {\n    aspect-ratio: 1 / 1;\n    width: var(--social-links-default-width);\n    min-width: 48px;\n    height: auto;\n  }\n}\n","/*------------------------------------*\\\n  #PROJECT-CARD\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n@use 'Abstracts/functions' as fn;\n\n.project-card {\n  box-shadow: 2px 5px 10px hsla(0, 0%, 0%, 0.2);\n  max-width: var(--project-card-max-width);\n\n  &__screenshot {\n    height: calc(7.5 * var(--h2-size));\n\n    &--light-blue-violet {\n      background-color: hsl(245, 45%, 63%);\n    }\n\n    &--orange {\n      background-color: hsl(32, 67%, 54%);\n    }\n\n    &--green {\n      background-color: hsl(105, 30%, 43%);\n    }\n\n    &--pink {\n      background-color: hsl(335, 45%, 63%);\n    }\n\n    &--light-blue {\n      background-color: hsl(209, 66%, 56%);\n    }\n  }\n\n  &__screenshot-inner {\n    display: flex;\n    flex-shrink: 0;\n    align-items: center;\n    justify-content: center;\n    padding: calc(2 * var(--h2-size)) var(--h2-size);\n    width: 100%;\n    height: 100%;\n  }\n\n  &__screenshot-text {\n    text-align: center;\n    color: hsl(0, 0%, 100%);\n    font-family: var(--title-font);\n    font-size: var(--h2-size);\n  }\n\n  &__info {\n    --info-height: calc(7 * var(--h3-size));\n\n    display: grid;\n    grid-template-areas:\n      'name        links'\n      'description description';\n    grid-template-rows:\n      calc((2 / 7) * var(--info-height))\n      calc((5 / 7) * var(--info-height));\n    grid-template-columns: 1fr auto;\n    padding-inline: 0.75rem;\n    height: var(--info-height);\n  }\n\n  &__name {\n    @extend %tertiary-title;\n\n    grid-area: name;\n    align-self: center;\n    justify-self: start;\n  }\n\n  &__project-links {\n    grid-area: links;\n    align-self: center;\n    justify-self: end;\n  }\n\n  &__description {\n    grid-area: description;\n    color: var(--text-color-main);\n    font-family: var(--text-font);\n  }\n}\n","/*------------------------------------*\\\n  #PROJECT-LINKS\n\\*------------------------------------*/\n\n.project-links {\n  display: flex;\n  gap: 1rem;\n  padding: 0;\n  list-style-type: none;\n\n  &__icon {\n    aspect-ratio: 1 / 1;\n    width: 2rem;\n    min-width: 32px;\n    height: auto;\n  }\n}\n","/*------------------------------------*\\\n  #CONTACT\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n@use 'Abstracts/mixins' as mxn;\n\n.contact {\n  --contact-gap: 1.5rem;\n  @extend %flex-col;\n\n  gap: var(--contact-gap);\n  background-color: var(--secondary-bgc);\n  // padding: 1rem var(--inline-padding-small) 5rem;\n  max-width: 450px;\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium {\n    padding: 0;\n  }\n\n  &__title {\n    @extend %secondary-title;\n\n    align-self: center;\n    color: hsl(0, 0%, 100%);\n  }\n\n  &__text,\n  &__address,\n  &__number,\n  &__email {\n    color: hsl(0, 0%, 100%);\n    font-family: var(--text-font);\n    font-style: normal;\n  }\n\n  &__text {\n    margin-top: calc(-0.5 * var(--contact-gap));\n    font-size: 1.25rem;\n  }\n\n  &__number {\n    margin-top: var(--contact-gap);\n    font-family: var(--text-font);\n  }\n\n  &__social-links {\n    align-self: center;\n    margin-top: var(--contact-gap);\n  }\n}\n","// This file contains all application-wide Sass mixins.\n\n@mixin medium-small {\n  @media screen and (min-width: 450px) {\n    @content;\n  }\n}\n\n@mixin medium {\n  @media screen and (min-width: 768px) {\n    @content;\n  }\n}\n\n@mixin large {\n  @media screen and (min-width: 1024px) {\n    @content;\n  }\n}\n\n@mixin extra-large {\n  @media screen and (min-width: 1524px) {\n    @content;\n  }\n}\n","/*------------------------------------*\\\n  #PAGE\n\\*------------------------------------*/\n\n/**\n * This file contains all global styles applied to the entire page.\n */\n\n.page {\n  display: flex;\n  flex-direction: column;\n  gap: 5rem;\n  width: 100vw;\n  overflow-x: hidden;\n}\n","/*------------------------------------*\\\n  #HEADER\n\\*------------------------------------*/\n\n/**\n * This file contains all styles related to the header of the site/application.\n */\n\n@use 'Abstracts/functions' as fn;\n@use 'Abstracts/placeholders';\n@use 'Abstracts/mixins' as mxn;\n@use 'sass:math';\n\n.header {\n  --social-links-margin-top-medium-small: 2rem;\n  --header-inner-padding-inline-medium: 2rem;\n  --social-links-bottom-offset-medium: 1rem;\n  --upper-padding-small: 1.5rem;\n\n  /**\n   * These calculations are done to make the gradient boundary always lie in\n   * roughly the same place relative to the photo.\n   */\n  --init-photo-width: 420;\n  --init-photo-height: 595;\n  --photo-aspect-ratio: calc(\n    var(--init-photo-width) / var(--init-photo-height)\n  );\n  --photo-width: calc(100vw - 2 * var(--padding-inline-small));\n  --photo-height: calc(var(--photo-width) / var(--photo-aspect-ratio));\n  --gradient-boundary: calc(\n    0.9 * (var(--photo-height) + var(--upper-padding-small))\n  );\n  --gradient-angle: 160deg;\n\n  display: flex;\n  justify-content: center;\n  background-image: linear-gradient(\n    var(--gradient-angle),\n    var(--secondary-bgc) var(--gradient-boundary),\n    var(--main-bgc) var(--gradient-boundary)\n  );\n  padding: var(--upper-padding-small) var(--padding-inline-small) 0;\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium-small {\n    /** \n     * 3rem is the footer's bottom padding.\n     */\n    --upper-padding-medium-small: 3rem;\n\n    /**\n     * The section is commented out because I didn't come up with a good\n     * solution for how to work around the fact that the `sqrt()` works only \n     * with unitless values. I'm leaving it here in chance I get some idea in\n     * the future.\n     */\n    /**\n     *                *****************************************---*************\n     *                *                                          |            *\n     *                *                                          |            *\n     *                *              *--************************ |            *\n     *                *              * |                       * |            *\n     *                *              * |                       * |            *\n     *                *              * |                       * m            *\n     *                *              * |                       * |            *\n     *                *              * h        PHOTO          * |            *\n     *                *              * |                       * |            *\n     *                *              * |                       * |            *\n     *                *      z       * |                       * |            *\n     *              --×**************×----------------------------            *\n     *              | *           *  * x                       *              *\n     *              | *        *     * |                       *              *\n     *              y *     * d      ×--************************              *\n     *       •      | *  *                                                    *\n     *       •      --×                                                       *\n     *       •     *  *                                                       *\n     *       •   *    *                                                       *\n     *       • *      *                                                       *\n     *       ×  <--------- gradient angle (formed with bullet-point lines)    *\n     *     *   •      *                                                       *\n     *   *       •    *                                                       *\n     *             •  *********************************************************\n     *               •\n     *\n     *\n     * By the law of cosines:\n     * y² = z² + d² - 2zd * cos(180deg - gradient-angle)\n     */\n    /* --z: calc((100vw - var(--init-photo-width) * 1px) / 2);\n    --d: calc(var(--z) / sin(180deg - var(--gradient-angle)));\n    --y-square: calc(\n      pow(var(--z), 2) + pow(var(--d), 2) - 2 * var(--z) * var(--d) *\n        cos(180deg - var(--gradient-angle))\n    );\n    --y: calc(sqrt(var(--y-square)));*/\n\n    /* I want h to be 83% of the photo height. */\n    /* --h: calc(0.83 * var(--init-photo-height) * 1px);\n    --m: calc(var(--upper-padding-medium-small) + var(--h));\n    --gradient-boundary: calc(var(--y) + var(--m)); */\n\n    --gradient-boundary: 50%;\n\n    background-image: linear-gradient(\n      var(--gradient-angle),\n      var(--secondary-bgc) var(--gradient-boundary),\n      var(--main-bgc) var(--gradient-boundary)\n    );\n\n    padding-top: var(--upper-padding-medium-small);\n    padding-inline: var(--padding-inline-medium-small);\n  }\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium {\n    --upper-padding-medium: 8rem;\n    --gradient-boundary: 55%;\n\n    background-image: linear-gradient(\n      var(--gradient-angle),\n      var(--secondary-bgc) var(--gradient-boundary),\n      var(--main-bgc) var(--gradient-boundary)\n    );\n\n    padding-top: var(--upper-padding-medium);\n    padding-inline: var(--padding-inline-medium);\n  }\n\n  @include mxn.large {\n    display: flex;\n    align-items: center;\n    background-image: linear-gradient(\n      var(--gradient-angle),\n      var(--secondary-bgc) var(--gradient-boundary),\n      var(--main-bgc) var(--gradient-boundary)\n    );\n    padding-top: 0;\n    padding-bottom: 0;\n    height: 100vh;\n  }\n\n  &__inner {\n    --header-inner-padding-top-medium: 8rem;\n\n    @extend %flex-col;\n\n    align-items: center;\n    max-width: 500px;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      display: block;\n      position: relative;\n      box-shadow: 2px 3px 10px 3px hsla(0, 0%, 0%, 0.125);\n      background-color: hsl(0, 0%, 100%);\n      padding-top: var(--header-inner-padding-top-medium);\n      padding-bottom: calc(\n        var(--social-links-bottom-offset-medium) +\n          var(--social-links-margin-top-medium-small) +\n          var(--social-links-default-width)\n      );\n      padding-inline: var(--header-inner-padding-inline-medium);\n      max-width: 100%;\n    }\n\n    @include mxn.large {\n      margin-left: calc(var(--init-photo-width) * 1px);\n      padding-top: 2rem;\n      /**\n       * This is to make the header contents the same width as the portfolio's. */\n      max-width: calc(\n        3 * var(--project-card-max-width) + 2 *\n          var(--portfolio-column-gap-large) - var(--init-photo-width) * 1px\n      );\n    }\n  }\n\n  &__photo-wrapper {\n    --photo-translate-factor: -5rem;\n    --photo-width-medium-unscaled: 30%;\n    --photo-height-medium-unscaled: calc(\n      var(--photo-width-medium-unscaled) / var(--photo-aspect-ratio)\n    );\n\n    /**\n     * `--upper-padding-medium-small` is the same length as the footer's bottom\n     * padding. I want the photo to have the same offset off the top \n     * of the page.\n     * \n     * (The formatting is Prettier's, by the way, so don't blame me for it.)\n     * \n     * PS: it's commented out because I didn't come up with a good\n     * solution for how to work around the fact that the scale factor must be\n     * unitless. I could strip the result of the calculation from its unit with\n     * Sass if the factor were static, but it's dynamic. I'm leaving it here\n     * in chance I get some idea in the future.\n     */\n    /* --photo-height-medium: calc(\n      var(--upper-padding-medium) + var(--header-inner-padding-top-medium) +\n        var(--photo-translate-factor) - var(--upper-padding-medium-small)\n    );\n    --photo-scale-factor: calc(\n      var(--photo-height-medium) / var(--photo-height-medium-unscaled)\n    ); */\n\n    position: relative;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      position: static;\n      float: left;\n      margin-right: 1.5rem;\n      margin-bottom: -7rem;\n      width: calc(var(--photo-width-medium-unscaled));\n    }\n\n    @include mxn.large {\n      position: absolute;\n      top: calc(-1 * (var(--init-photo-height) * 1px - 100%) / 2);\n      left: calc(-1px * var(--init-photo-width));\n      float: none;\n      margin: 0;\n      width: calc(var(--init-photo-width) * 1px);\n      max-width: calc(80vh / var(--photo-aspect-ratio));\n    }\n  }\n\n  &__photo {\n    box-shadow: 0 0 5px 2px hsla(0, 0%, 0%, 0.1);\n    height: 100%;\n    aspect-ratio: var(--init-photo-width) / var(--init-photo-height);\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      transform: scale(1.4) translateY(var(--photo-translate-factor));\n      transform-origin: bottom right;\n      box-shadow: 1px 1px 5px 0 hsla(0, 0%, 0%, 0.4);\n      width: 100%;\n      height: auto;\n    }\n\n    @include mxn.large {\n      transform: none;\n    }\n  }\n\n  &__name {\n    position: absolute;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    padding-inline: fn.rem(7);\n    text-shadow: 0 fn.rem(2) fn.rem(2) hsla(0, 0%, 0%, 0.3);\n    line-height: 1.1;\n    color: hsl(0, 0%, 100%);\n    font-family: var(--title-font);\n    font-size: 3rem;\n    font-weight: 400;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      top: -1.25em;\n      right: auto;\n      bottom: auto;\n      left: 24%;\n      width: auto;\n      text-shadow: 0 fn.rem(3) fn.rem(2) hsla(0, 0%, 0%, 0.3);\n      white-space: nowrap;\n    }\n\n    @include mxn.large {\n      top: auto;\n      right: 0;\n      bottom: 0;\n      left: 0;\n      text-align: center;\n    }\n  }\n\n  &__about {\n    @extend %secondary-title;\n\n    margin-top: 1em;\n    text-align: center;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      margin: 0;\n      white-space: nowrap;\n    }\n\n    @include mxn.large {\n      text-align: left;\n    }\n  }\n\n  &__text {\n    color: var(--text-color-main);\n    font-family: var(--text-font);\n    font-size: 1.3rem;\n  }\n\n  &__social-links {\n    margin-top: 1rem;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium-small {\n      margin-top: var(--social-links-margin-top-medium-small);\n    }\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      position: absolute;\n      right: var(--header-inner-padding-inline-medium);\n      bottom: var(--social-links-bottom-offset-medium);\n    }\n  }\n}\n","/*------------------------------------*\\\n  #PORTFOLIO\n\\*------------------------------------*/\n\n@use 'Abstracts/placeholders';\n@use 'Abstracts/mixins' as mxn;\n\n.portfolio {\n  @extend %flex-col;\n\n  align-items: center;\n  padding-inline: var(--padding-inline-small);\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium-small {\n    padding-inline: var(--padding-inline-medium-small);\n  }\n\n  @include mxn.medium {\n    padding-inline: var(--padding-inline-medium);\n  }\n\n  &__title {\n    @extend %secondary-title;\n\n    align-self: center;\n    margin-bottom: 1rem;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      /** \n       * To align the title with the project cards.\n       */\n      width: 100%;\n      max-width: calc(\n        2 * var(--project-card-max-width) + var(--portfolio-column-gap-medium)\n      );\n    }\n\n    @include mxn.large {\n      /* 3 rem is the column gap of the portfolio__content element. */\n      max-width: calc(\n        3 * var(--project-card-max-width) + 2 *\n          var(--portfolio-column-gap-large)\n      );\n    }\n  }\n\n  &__content {\n    @extend %flex-col;\n\n    gap: 2rem;\n    align-items: center;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      display: grid;\n      grid-template: 1fr 1fr 1fr / 1fr 1fr;\n      row-gap: 4rem;\n      column-gap: var(--portfolio-column-gap-medium);\n    }\n\n    @include mxn.large {\n      grid-template: 1fr 1fr / 1fr 1fr 1fr;\n      column-gap: var(--portfolio-column-gap-large);\n    }\n  }\n}\n","/*------------------------------------*\\\n  #FOOTER\n\\*------------------------------------*/\n\n/**\n * This file contains all styles related to the footer of the site/application.\n */\n\n@use 'Abstracts/mixins' as mxn;\n@use 'Abstracts/placeholders';\n\n.footer {\n  @extend %flex-col;\n\n  gap: 5rem;\n  align-items: center;\n  background-color: var(--secondary-bgc);\n  padding: 2rem 1rem 0;\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium-small {\n    /**\n     * 3rem bottom padding is necessary because of the font's internal padding */\n    padding: 2rem var(--padding-inline-medium-small) 3rem;\n  }\n\n  /* stylelint-disable-next-line order/order */\n  @include mxn.medium {\n    flex-direction: row;\n    gap: 2rem;\n    justify-content: space-between;\n  }\n\n  @include mxn.large {\n    justify-content: center;\n  }\n\n  &__image-wrapper {\n    width: 100vw;\n    max-width: 450px;\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium-small {\n      width: 100%;\n    }\n\n    /* stylelint-disable-next-line order/order */\n    @include mxn.medium {\n      /**\n       * To vertically centralize the image \n       * (footer's top and bottom paddings are different)\n       */\n      margin-top: 1rem;\n      width: auto;\n      max-width: 300px;\n    }\n  }\n\n  &__image {\n    aspect-ratio: 450 / 675;\n    width: 100%;\n    height: auto;\n  }\n}\n","/*------------------------------------*\\\n  #VARIABLES\n\\*------------------------------------*/\n\n/* This file contains all application-wide variables. */\n\n@use 'Abstracts/functions' as fn;\n\n:root {\n  --main-bgc: hsl(0, 0%, 100%);\n  --secondary-bgc: hsl(192, 100%, 40%);\n  --h2-size: 2.5rem;\n  --h3-size: 1.75rem;\n  --text-color-main: hsl(0, 1%, 35%);\n  --title-font: 'Bona Nova', serif;\n  --text-font: 'Noto Sans', sans-serif;\n  --padding-inline-small: 1.25rem;\n  --padding-inline-medium-small: 2rem;\n  --padding-inline-medium: 5rem;\n  --social-links-default-width: 3rem;\n  --project-card-max-width: #{fn.rem(400)};\n  --portfolio-column-gap-medium: 2.5rem;\n  --portfolio-column-gap-large: 3rem;\n}\n"],"sourceRoot":""}]);
